import selectAddr from '@/components/selectAddr.vue'
import waitingOrder from '@/components/waitingOrder.vue'
export default {
    data() {
        return {
            attr: {
                show: false,
            },
            waiting_data: {
                show: false
            },
            state: 2,
            arr: [1, 1, 1, 2, 2, 2, 1, 1, 2 ,2, 2],
        }
    },
    components: {
        selectAddr,
        waitingOrder
    },
    methods: {
        changeState(){
            let durantion = () => {
                this.state = this.arr[Math.round(Math.random()*10)+1];
                setTimeout(durantion, 3000, this)
            }
            durantion()
        },
        select(){
            this.attr.show = true;
        },
        getData(){
            this.attr.show = false;
        },
        getState(){
            this.waiting_data.show = false;
        },
        callCar(){
            this.waiting_data.show = true;
        },
        detail(){
            this.$router.push('/driver_detail');
        },
        to_wallet(){
            this.$router.push('/driver_wallet');
            
        },
        confirm(){
            
        },
        share(){
            this.$router.push('./driver_share')
        }
    },
    mounted(){
        this.changeState()
    }
}


